/**
 * WordPress dependencies
 */
import { __, sprintf } from '@wordpress/i18n';
import { useEntityRecords } from '@wordpress/core-data';
import { useSelect } from '@wordpress/data';
import { createInterpolateElement, useState } from '@wordpress/element';
import {
	Button,
	ComboboxControl,
	Modal,
	__experimentalSpacer as Spacer, // eslint-disable-line
	__experimentalHStack as HStack, // eslint-disable-line
} from '@wordpress/components';
import { seen, edit } from '@wordpress/icons';

/**
 * Template selector component for menu templates
 *
 * @param {Object}   props                Component props
 * @param {string}   props.value          Selected template slug
 * @param {Function} props.onChange       Callback when template changes
 * @param {string}   props.templateArea   Template area (default: 'menu')
 * @param {string}   props.label          Label for the selector
 * @param {string}   props.help           Help text for the selector
 * @param {Object}   props.previewOptions Preview modal options (width, customWidth, etc.)
 */
export default function TemplateSelector( {
	value,
	onChange,
	templateArea = 'menu',
	label = __( 'Menu Template', 'ollie-menu-designer' ),
	help = null,
	previewOptions = {},
} ) {
	// State for preview modal
	const [ isPreviewOpen, setIsPreviewOpen ] = useState( false );
	// State for iframe height
	const [ iframeHeight, setIframeHeight ] = useState( '600px' );
	// State for loading
	const [ isIframeLoading, setIsIframeLoading ] = useState( true );

	// Get the Url for the template part screen in the Site Editor.
	const { siteUrl, currentTheme, layout } = useSelect( ( select ) => {
		const { getSite, getCurrentTheme } = select( 'core' );
		const editorSettings = select( 'core/editor' ).getEditorSettings();
		return {
			siteUrl: getSite()?.url,
			currentTheme: getCurrentTheme()?.stylesheet,
			layout: editorSettings?.__experimentalFeatures?.layout,
		};
	}, [] );

	// Ensure siteUrl uses the same protocol as the current page
	const secureSiteUrl =
		siteUrl && window.location.protocol === 'https:'
			? siteUrl.replace( /^http:\/\//, 'https://' )
			: siteUrl || '';
	const menuTemplateUrl = secureSiteUrl
		? secureSiteUrl +
		  `/wp-admin/site-editor.php?postType=wp_template_part&categoryId=${ templateArea }`
		: '';

	// Fetch all template parts.
	const { hasResolved, records } = useEntityRecords(
		'postType',
		'wp_template_part',
		{
			per_page: -1,
		}
	);

	let templateOptions = [];

	// Filter the template parts for those in the specified area.
	if ( hasResolved ) {
		templateOptions = records
			.filter( ( item ) => item.area === templateArea )
			.map( ( item ) => ( {
				label: item.title.rendered,
				value: item.slug,
			} ) );
	}

	const hasTemplates = templateOptions.length > 0;
	const selectedTemplateAndExists = value
		? templateOptions.some( ( option ) => option.value === value )
		: true;

	// Handle iframe load to measure its height
	const handleIframeLoad = ( event ) => {
		const iframe = event.target;

		// Wait for content to be ready
		const measureHeight = () => {
			const iframeDoc = iframe.contentDocument;

			// Ensure document is fully loaded
			if ( iframeDoc.readyState !== 'complete' ) {
				setTimeout( measureHeight, 50 );
				return;
			}

			const body = iframeDoc.body;

			// Temporarily give iframe more height to measure content
			iframe.style.height = '2000px';

			// Force reflow
			// eslint-disable-next-line no-unused-expressions
			body.offsetHeight;

			// Get the actual content height
			const contentHeight = Math.max(
				body.scrollHeight,
				body.offsetHeight,
				body.clientHeight
			);

			// Reset iframe height
			iframe.style.height = '100%';

			// Calculate final height with padding
			const heightWithPadding = contentHeight + 50; // 40px original padding + 10px extra
			const viewportHeight = window.innerHeight;
			const maxHeight = viewportHeight - 120; // Leave room for modal header

			// Set the appropriate height
			const finalHeight =
				heightWithPadding > maxHeight
					? `${ maxHeight }px`
					: `${ heightWithPadding }px`;

			setIframeHeight( finalHeight );
			setIsIframeLoading( false );
		};

		// Start measuring
		measureHeight();
	};

	// Reset loading state when modal opens or template changes
	useState( () => {
		if ( isPreviewOpen ) {
			setIsIframeLoading( true );
		}
	}, [ isPreviewOpen, value ] );

	const defaultHelp = createInterpolateElement(
		hasTemplates
			? sprintf(
					// translators: %s: template area name
					__(
						'Manage %s templates in the <a>Site Editor</a>.',
						'menu-designer'
					),
					templateArea
			  )
			: sprintf(
					// translators: %s: template area name
					__(
						'No %s templates found. Create one now in the <a>Site Editor</a>.',
						'menu-designer'
					),
					templateArea
			  ),
		{
			a: (
			<a // eslint-disable-line
					href={ menuTemplateUrl }
					target="_blank"
					rel="noreferrer"
				/>
			),
		}
	);

	// Calculate modal width based on previewOptions
	const modalWidth = ( () => {
		const { width, customWidth } = previewOptions;
		switch ( width ) {
			case 'content':
				return layout?.contentSize || '650px';
			case 'wide':
				return layout?.wideSize || '1200px';
			case 'full':
				return '90%';
			case 'custom':
				return customWidth ? `${ customWidth }px` : '650px';
			default:
				return '650px';
		}
	} )();

	return (
		<>
			<ComboboxControl
				label={ label }
				value={ value }
				options={ templateOptions }
				onChange={ onChange }
				help={ help || defaultHelp }
			/>
			<Spacer marginTop={ 5 } />
			{ value && selectedTemplateAndExists && (
				<>
					<HStack>
						<Button
							isSecondary
							icon={ seen }
							onClick={ () => setIsPreviewOpen( true ) }
						>
							{ __( 'Preview', 'ollie-menu-designer' ) }
						</Button>
						<Button
							isTertiary
							icon={ edit }
							href={ `${ secureSiteUrl }/wp-admin/site-editor.php?p=%2Fwp_template_part%2F${
								currentTheme || ''
							}%2F%2F${ value }&canvas=edit` }
							target="_blank"
						>
							{ __( 'Edit Template', 'ollie-menu-designer' ) }
						</Button>
					</HStack>
					<Spacer marginTop={ 6 } />
				</>
			) }
			{ isPreviewOpen && (
				<Modal
					title={ sprintf(
						__( 'Template Preview: %s', 'ollie-menu-designer' ),
						templateOptions.find(
							( option ) => option.value === value
						)?.label || value
					) }
					onRequestClose={ () => {
						setIsPreviewOpen( false );
						setIframeHeight( '600px' ); // Reset height when closing
						setIsIframeLoading( true ); // Reset loading state
					} }
					className="mega-menu-block-preview-modal"
					aria-label={ __( 'Preview template', 'ollie-menu-designer' ) }
					style={ {
						maxWidth: '90%',
						width: modalWidth,
					} }
				>
					<div
						style={ {
							width: '100%',
							height: isIframeLoading ? '200px' : iframeHeight,
							transition: 'height 0.3s ease',
							overflow: 'hidden',
							position: 'relative',
						} }
					>
						{ isIframeLoading && (
							<div
								style={ {
									position: 'absolute',
									top: '50%',
									left: '50%',
									transform: 'translate(-50%, -50%)',
									textAlign: 'center',
								} }
							>
								<span className="components-spinner" />
								<p style={ { marginTop: '10px' } }>
									{ __(
										'Loading preview…',
										'menu-designer'
									) }
								</p>
							</div>
						) }
						<iframe
							key={ value } // Force iframe to reload when value changes
							src={ `${ secureSiteUrl }/?ollie_mega_menu_preview=${ value }` }
							style={ {
								width: '100%',
								height: '100%',
								border: 'none',
								opacity: isIframeLoading ? 0 : 1,
								transition: 'opacity 0.3s ease',
							} }
							title={ __(
								'Template Preview',
								'menu-designer'
							) }
							onLoad={ handleIframeLoad }
						/>
					</div>
				</Modal>
			) }
		</>
	);
}

