/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody } from '@wordpress/components';
import { createHigherOrderComponent } from '@wordpress/compose';
import { addFilter } from '@wordpress/hooks';

/**
 * Internal dependencies
 */
import TemplateSelector from '../shared/TemplateSelector';

/**
 * Add mobile menu controls to the navigation block
 */
const withMobileMenuControls = createHigherOrderComponent( ( BlockEdit ) => {
	return ( props ) => {
		const { name, attributes, setAttributes } = props;

		// Only add controls to navigation block
		if ( name !== 'core/navigation' ) {
			return <BlockEdit { ...props } />;
		}

		const { mobileMenuSlug } = attributes;

		return (
			<>
				<BlockEdit { ...props } />
				<InspectorControls group="settings">
					<PanelBody
						title={ __( 'Mobile Menu', 'ollie-menu-designer' ) }
						initialOpen={ false }
					>
						<TemplateSelector
							value={ mobileMenuSlug }
							onChange={ ( value ) =>
								setAttributes( { mobileMenuSlug: value } )
							}
							templateArea="menu"
							label={ __(
								'Mobile Menu Template',
								'menu-designer'
							) }
							help={ __(
								'Select a template to use as the mobile menu.',
								'menu-designer'
							) }
						/>
					</PanelBody>
				</InspectorControls>
			</>
		);
	};
}, 'withMobileMenuControls' );

/**
 * Add mobile menu attributes to navigation block
 * @param {Object} settings - Block settings
 * @param {string} name - Block name
 * @return {Object} Modified settings
 */
const addMobileMenuAttributes = ( settings, name ) => {
	if ( name !== 'core/navigation' ) {
		return settings;
	}

	return {
		...settings,
		attributes: {
			...settings.attributes,
			mobileMenuSlug: {
				type: 'string',
				default: '',
			},
		},
	};
};

// Add filters
addFilter(
	'editor.BlockEdit',
	'ollie-mega-menu/with-mobile-menu-controls',
	withMobileMenuControls
);

addFilter(
	'blocks.registerBlockType',
	'ollie-mega-menu/mobile-menu-attributes',
	addMobileMenuAttributes
);

